}
:root {
    --backdrop-visible-background: var(--color-light-bg-overlay);
    --backdrop-hidden-background: transparent;
}
:root {
    --drawer-width: 500px;
}
.drawer__component_1yr25 {
    position: fixed;
    top: 0;
    height: 100%;
    width: var(--drawer-width);
    flex: 1;
    overflow: auto;
    will-change: transform;
}
.drawer__rightPlacement_1yr25 {
    right: 0;
    align-self: flex-end;
}
.drawer__leftPlacement_1yr25 {
    left: 0;
    align-self: flex-start;
}
.drawer__content_1yr25 {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;
}
.drawer__simplebar_1yr25 {
    height: 100vh;
}
/* enter */
.drawer__enterRight_1yr25 {
    transform: translateX(100%);
}
.drawer__enterLeft_1yr25 {
    transform: translateX(-100%);
}
.drawer__contentEnter_1yr25 {
    opacity: 0;
}
.drawer__backdropEnter_1yr25 {
    background-color: var(--backdrop-hidden-background);
}
.drawer__enterActive_1yr25 {
    transition: transform 0.3s ease-in-out;
    transform: translateX(0);
}
.drawer__backdropEnterActive_1yr25,
.drawer__backdropEnterDone_1yr25 {
    transition: background 0.3s ease-in-out;
    background-color: var(--backdrop-visible-background);
}
.drawer__contentEnterActive_1yr25 {
    transition: opacity 0.2s ease-in-out 0.3s;
    opacity: 1;
}
/* exit */
.drawer__exit_1yr25 {
    transform: translateX(0);
}
.drawer__backdropExit_1yr25 {
    background-color: var(--backdrop-visible-background);
}
.drawer__contentExit_1yr25 {
    opacity: 1;
}
.drawer__exitActiveRight_1yr25 {
    transition: transform 0.25s ease-in-out 0.1s;
    transform: translateX(100%);
}
.drawer__exitActiveLeft_1yr25 {
    transition: transform 0.25s ease-in-out 0.1s;
    transform: translateX(-100%);
}
.drawer__backdropExitActive_1yr25,
.drawer__backdropExitDone_1yr25 {
    transition: background 0.25s ease-in-out 0.1s;
    background-color: var(--backdrop-hidden-background);
}
.drawer__contentExitActive_1yr25 {
    opacity: 0;
    transition: opacity 0.12s ease-in-out;
}
