.side-panel__appear_1v4rg,
.side-panel__enter_1v4rg {
    opacity: 0;
    transform: translateY(15px);
}

.side-panel__appearActive_1v4rg,
.side-panel__enterActive_1v4rg {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 200ms ease-in, transform 200ms ease-in;
}

.side-panel__exit_1v4rg {
    opacity: 1;
    transform: translateY(0);
}

.side-panel__exitActive_1v4rg,
.side-panel__exitDone_1v4rg {
    opacity: 0;
    transform: translateY(15px);
    transition: opacity 200ms ease-out, transform 200ms ease-out;
}
