:root {
    --backdrop-visible-background: var(--color-light-bg-overlay);
    --backdrop-hidden-background: transparent;
}
.backdrop__backdrop_4vskd {
    z-index: -1;
    position: fixed;
    right: 0;
    bottom: 0;
    top: 0;
    left: 0;
    -webkit-tap-highlight-color: transparent; /* убирает хайлайт */
}
.backdrop__appear_4vskd,
.backdrop__enter_4vskd {
    background-color: var(--backdrop-hidden-background);
}
.backdrop__appearActive_4vskd,
.backdrop__enterActive_4vskd,
.backdrop__appearDone_4vskd,
.backdrop__enterDone_4vskd {
    background-color: var(--backdrop-visible-background);
    transition: background-color 200ms ease-in;
}
.backdrop__exit_4vskd {
    background-color: var(--backdrop-visible-background);
}
.backdrop__exitActive_4vskd,
.backdrop__exitDone_4vskd {
    background-color: var(--backdrop-hidden-background);
    transition: background-color 200ms ease-out;
}
.backdrop__invisible_4vskd {
    opacity: 0;
}
